body {
  background-image: url('C:\Users\MI\Downloads\Project\client\src\components\hiiiii.webp');
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  }
  
  .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 50px;
  animation: slideInRight 1s ease-in-out;
  }
  
  h1 {
  font-size: 48px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 40px;
  animation: fadeIn 1s ease-in-out;
  }
  
  .life-insurance-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
  }
  
  .life-insurance-card {
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 40px;
  text-align: center;
  background-color: tran;
  animation: slideInUp 1s ease-in-out;
  }
  
  .life-insurance-card h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  }
  
  .life-insurance-card p {
  font-size: 18px;
  margin-bottom: 30px;
  }
  
  .life-insurance-card button {
  background-color: #21221f;
  color: #ffffff;
  border: none;
  border-radius: 25px;
  padding: 10px 30px;
  font-size: 18px;
  font-weight: bold;
  transition: background-color 0.3s;
  }
  
  .life-insurance-card button:hover {
  background-color: #d61522;
  }
  
  .life-insurance-card img {
  width: 80%;
  margin-bottom: 30px;
  }
  
  @media screen and (max-width: 900px) {
  .life-insurance-grid {
  grid-template-columns: repeat(2, 1fr);
  }
  }
  
  @media screen and (max-width: 600px) {
  .life-insurance-grid {
  grid-template-columns: 1fr;
  }
  
  h1 {
  font-size: 36px;
  }
  
  .life-insurance-card img {
  width: 60%;
  }
  
  .life-insurance-card p {
  font-size: 16px;
  }
  
  .life-insurance-card button {
  font-size: 16px;
  padding: 8px 24px;
  }
  }
  
  @keyframes slideInUp {
  0% {
  transform: translateY(100%);
  }
  100% {
  transform: translateY(0);
  }
  }
  
  @keyframes slideInRight {
  0% {
  transform: translateX(-100%);
  }
  100% {
  transform: translateX(0);
  }
  }
  
  @keyframes fadeIn {
  0% {
  opacity: 0;
  }
  100% {
  opacity: 1;
  }
  }