.App-login {
  background-color: #f1f2f2;
  min-height: 250px;
  min-width: 350px;
  padding: 20px;
  margin-top: 40px;
  display: block;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
