.health-insurance-plans-container {
  margin: 50px auto;
  max-width: 800px;
  text-align: center;
  /* Add a background image */
  /* background-image: url('C:\Users\MI\Downloads\Project\client\src\components\homebg.jpeg'); */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.health-insurance-plans-container h1 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 50px;
}

.health-insurance-plan {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 50px;
  padding: 40px;
  text-align: left;
}

.health-insurance-plan h2 {
  color: #333;
  font-size: 24px;
  margin-bottom: 30px;
}

.health-insurance-plan ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.health-insurance-plan li {
  font-size: 18px;
  line-height: 2;
}

.health-insurance-plan p {
  font-size: 24px;
  font-weight: bold;
  margin-top: 30px;
}

@media (max-width: 767px) {
  .health-insurance-plans-container h1 {
    font-size: 28px;
  }

  .health-insurance-plan {
    padding: 20px;
  }

  .health-insurance-plan h2 {
    font-size: 20px;
  }

  .health-insurance-plan li {
    font-size: 16px;
  }

  .health-insurance-plan p {
    font-size: 20px;
  }
}
