.about-us {
  padding: 50px;
  background-image: url('C:\Users\MI\Downloads\Project\client\src\components\123.jpg'); 
  background-size: 100% 100%;

  background-position: center center;
  color: rgb(0, 0, 0);
}

h1, h2 {
  animation: fade-in 1s ease-out;
}

@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.contact-list li {
  display: inline-block;
  margin-right: 20px;
}

.contact-list a {
  color: rgb(255, 0, 0);
  text-decoration: none;
  font-size: 20px;
  transition: all 0.3s ease-out;
}

.contact-list a:hover {
  transform: scale(1.1);
}

/* Add scrolling effect to the page */
html {
  scroll-behavior: smooth;
}
