  .health-insurance-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #101010;
    background-color: rgba(11, 202, 232, 0.1);
    box-shadow: 0 0 10px rgba(12, 159, 200, 0.5);
  }
  
  
  .health-insurance-content h1,
.health-insurance-content p {
  background: #101010;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: #fff;
}

/* Example blue background color */
body {
  background-color: #0077be;
}

  
  
  
  .health-insurance-content button {
    background-color: #1a1b1b;
    color: #ffffff;
    border: none;
    border-radius: 25px;
    padding: 10px 30px;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    transition: background-color 0.3s;
  }
  